import React, { useState, useEffect, useRef } from "react";

const NOTE_NAMES = ["C", "Db", "D", "Eb", "E", "F", "Gb", "G", "Ab", "A", "Bb", "B"];
const FIFTHS_ORDER = ["C", "G", "D", "A", "E", "B", "Gb", "Db", "Ab", "Eb", "Bb", "F"];

function getRandomNoteWithQuality() {
    const note = NOTE_NAMES[Math.floor(Math.random() * NOTE_NAMES.length)];
    const quality = Math.random() < 0.5 ? "major" : "minor";
    return `${note} ${quality}`;
}

function getFifthsNoteWithQuality(currentNote, direction = 1) {
    // direction: 1 for 5ths, -1 for 4ths
    const current = currentNote ? currentNote.split(" ")[0] : "C";
    const idx = FIFTHS_ORDER.indexOf(current);
    let nextIdx = (idx + direction + FIFTHS_ORDER.length) % FIFTHS_ORDER.length;
    const nextNote = FIFTHS_ORDER[nextIdx];
    const quality = Math.random() < 0.5 ? "major" : "minor";
    return `${nextNote} ${quality}`;
}

export default function RandomNoteDisplay() {
    const [note, setNote] = useState(getRandomNoteWithQuality());
    const [nextNote, setNextNote] = useState(getRandomNoteWithQuality());
    const [intervalSec, setIntervalSec] = useState(5);
    const [progress, setProgress] = useState(0);
    const [moveMode, setMoveMode] = useState("random"); // "random", "fifths", "fourths"
    const progressRef = useRef();

    useEffect(() => {
        setProgress(0);
        let start = Date.now();
        let frame;
        function update() {
            const elapsed = (Date.now() - start) / 1000;
            setProgress(Math.min(elapsed / intervalSec, 1));
            if (elapsed < intervalSec) {
                frame = requestAnimationFrame(update);
            }
        }
        update();
        progressRef.current = setInterval(() => {
            setNote(nextNote);
            if (moveMode === "fifths") {
                setNextNote(getFifthsNoteWithQuality(nextNote, 1));
            } else if (moveMode === "fourths") {
                setNextNote(getFifthsNoteWithQuality(nextNote, -1));
            } else {
                setNextNote(getRandomNoteWithQuality());
            }
            setProgress(0);
            start = Date.now();
            update();
        }, intervalSec * 1000);
        return () => {
            clearInterval(progressRef.current);
            cancelAnimationFrame(frame);
        };
    }, [intervalSec, nextNote, moveMode]);

    const handleChange = (e) => {
        const value = Math.max(1, Number(e.target.value));
        setIntervalSec(value);
    };

    const handleModeChange = (e) => {
        const mode = e.target.value;
        setMoveMode(mode);
        if (mode === "fifths") {
            setNextNote(getFifthsNoteWithQuality(note, 1));
        } else if (mode === "fourths") {
            setNextNote(getFifthsNoteWithQuality(note, -1));
        } else {
            setNextNote(getRandomNoteWithQuality());
        }
    };

    return (
        <div className="flex flex-col items-center justify-center p-8 text-black">
            <div className="text-6xl font-bold text-blue-700 mb-4">{note}</div>
            <div className="mb-4 text-lg text-green-700">
                Next: <span className="font-bold">{nextNote}</span>
            </div>
            <div className="w-full max-w-md mb-4">
                <div className="h-4 bg-gray-200 rounded-full overflow-hidden">
                    <div
                        className="h-4 bg-blue-500"
                        style={{ width: `${progress * 100}%` }}
                    ></div>
                </div>
            </div>
            <label className="mb-2">
                Change interval (s):&nbsp;
                <input
                    type="number"
                    min="1"
                    step="1"
                    value={intervalSec}
                    onChange={handleChange}
                    className="border rounded px-2 py-1"
                />
            </label>
            <div className="flex gap-4 mt-4">
                <label className="flex items-center">
                    <input
                        type="radio"
                        value="random"
                        checked={moveMode === "random"}
                        onChange={handleModeChange}
                        className="mr-2"
                    />
                    Random
                </label>
                <label className="flex items-center">
                    <input
                        type="radio"
                        value="fifths"
                        checked={moveMode === "fifths"}
                        onChange={handleModeChange}
                        className="mr-2"
                    />
                    Move in 5ths
                </label>
                <label className="flex items-center">
                    <input
                        type="radio"
                        value="fourths"
                        checked={moveMode === "fourths"}
                        onChange={handleModeChange}
                        className="mr-2"
                    />
                    Move in 4ths
                </label>
            </div>
        </div>
    );
}